@include desktop() {
  #skills {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 60px 20px;

    h2 {
      font-size: 32px;
      font-weight: 700;
      margin-bottom: 40px;
      color: #222;
    }

    .skill-list {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 32px;
      max-width: 1000px;
      width: 100%;
      padding: 0;
      margin: 0;

      li {
        list-style: none;
        background-color: #ffffff;
        border: 1px solid #e0e0e0;
        border-radius: 16px;
        padding: 48px 20px;
        text-align: center;
        font-size: 22px;
        font-weight: 700;
        color: #222;
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.06);

        // 선택적 효과 - 약한 강조선
        position: relative;

        &::after {
          content: "";
          display: block;
          height: 4px;
          width: 50px;
          background-color: #99c7fc;
          border-radius: 2px;
          margin: 12px auto 0;
        }
      }
    }
  }
}
