@include tablet() {
  #skills {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px 16px;

    h2 {
      font-size: 28px;
      font-weight: 700;
      margin-bottom: 36px;
      color: #222;
    }

    .skill-list {
      display: grid;
      grid-template-columns: repeat(2, 1fr); // 2열
      gap: 28px 32px;
      width: 90%;
      max-width: 900px;
      padding: 0;
      margin: 0;

      li {
        list-style: none;
        background-color: #ffffff;
        border: 1px solid #e0e0e0;
        border-radius: 14px;
        padding: 40px 20px;
        text-align: center;
        font-size: 20px;
        font-weight: 700;
        color: #222;
        box-shadow: 0 3px 8px rgba(0, 0, 0, 0.05);
        position: relative;

        &::after {
          content: "";
          display: block;
          height: 4px;
          width: 45px;
          background-color: #99c7fc;
          border-radius: 2px;
          margin: 14px auto 0;
        }
      }
    }
  }
}
