@include desktop() {
  #contact {
    display: flex;
    justify-content: space-between;
    width: 1080px;
    margin: 0 auto;

    .contact-inner {
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      width: 460px;
      padding: 20px 0;
    }

    .contact-left {
      background-color: #fff;
      box-shadow: 1px 1px 20px 1px rgba(0, 0, 0, 0.08);

      h2 {
        font-size: 28px;
      }

      p {
        width: 300px;
        margin: 0 auto;
        text-align: center;
      }

      .img {
        width: 300px;
        height: 260px;
        margin: 0 auto;
        background-image: url("./letter.jpg");
        background-size: cover;
        background-position: center;
      }
    }

    .contact-right {
      form {
        input,
        textarea {
          width: 460px;
          font-family: "Nanum Gothic", sans-serif;
          font-size: 16px;
          color: rgba(0, 0, 0, 0.7);
          border: 1px solid rgba(0, 0, 0, 0.25);
          margin-bottom: 16px;

          &:focus {
            outline: none;
          }
        }

        input {
          height: 40px;
          padding: 0 20px;
        }

        textarea {
          padding: 16px 20px;
          resize: none;
        }

        button {
          width: 460px;
          height: 40px;
          margin: 0 auto;
          color: #fff;
          border: none;
          background-color: #525e75;
          cursor: pointer;
          transition: background-color 0.2s, box-shadow 0.2s;

          &:hover {
            background-color: #3c5788;
            box-shadow: 1px 1px 8px 1px rgba(0, 0, 0, 0.25);
          }
        }
      }
    }
  }
}
