@include mobile() {
  #skills {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px 16px;

    h2 {
      font-size: 24px;
      font-weight: 700;
      margin-bottom: 24px;
      color: #222;
    }

    .skill-list {
      display: grid;
      grid-template-columns: 1fr; // 모바일은 한 줄에 하나
      gap: 20px;
      width: 100%;
      padding: 0;
      margin: 0;

      li {
        list-style: none;
        background-color: #ffffff;
        border: 1px solid #e0e0e0;
        border-radius: 12px;
        padding: 32px 16px;
        text-align: center;
        font-size: 18px;
        font-weight: 600;
        color: #222;
        box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
        position: relative;

        &::after {
          content: "";
          display: block;
          height: 3px;
          width: 36px;
          background-color: #99c7fc;
          border-radius: 2px;
          margin: 10px auto 0;
        }
      }
    }
  }
}
